body {
  margin: 0;
  background-color: #222;
  color: #dcd4c2;
  font-family: Courier;
  font-size: 1em;
  line-height: 1.4em;
  display: grid;
  grid-template-columns: 15% 85%;
  grid-template-rows: 4em auto 3em;
  grid-template-areas:
          "header header"
          "nav main"
          "footer footer";
  justify-items: stretch;
  align-items: start;
}

#adminPageHeaderGrid {
  padding: .4em;
  font-size: 1.2em;
  grid-area: header;
  display: grid;
  grid-template-columns: 2fr 3fr auto;
  grid-template-rows: auto;
  justify-content: space-between;
  background-color: #000000;
  //border: 1px darkslategray solid;
}

nav {
  padding: 0 .3em;
  grid-area: nav;
  font-size: 1.2em;
}

nav > ul > li > ul {
  font-size: .8em;
  margin-left: .8em;
}

main {
  padding: 0 1em 0 0;
  grid-area: main;
}

#adminPageFooterGrid {
  padding: .3em;
  grid-area: footer;
  justify-self: center;
  align-self: end;
}

a {
  color: #2ba6cb;
  text-decoration: none;
  padding: .1em;
}

a:hover {
  background-color: #9a9b9a;
  color: #0A246A;
}

@media print {
  body {
    background-color: white;
    color: black;
  }
}